export default {
	data() {
		return {
			/* 轮播图 */
			swiperList: [{}, {}],
			/* 商家列表 */
			itemList: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}]
		}
	},
	onReachBottom() {
		uni.showLoading({
			title: '加载中',
			mask: true
		})
		setTimeout(function() {
			uni.hideLoading()
		}, 1000);
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		/* 页面跳转 */
		jump2creditUser() {
			uni.navigateTo({
				url: '/pages/credit/user'
			})
		},
		jump2listCreditLog() {
			uni.navigateTo({
				url: '/pages/list/credit/log'
			})
		},
		jump2search() {
			uni.navigateTo({
				url: '/pages/search/index'
			})
		},
		jump2searchResult() {
			uni.navigateTo({
				url: '../search/result'
			})
		},
		jump2item(item, index) {
			uni.navigateTo({
				url: '/pages/item/index'
			})
		},
	}
}
